import React, { Component } from "react";

class NotFound extends Component {
  render() {
    return (
      <div className="app">
        <header>
          <h1>404 - not found</h1>
        </header>

        <main>
          <p>Go away</p>
        </main>
      </div>
    );
  }
}

export default NotFound;
